














































































































































































































import {Component, Vue, Watch} from 'vue-property-decorator';
import axios from "axios";
import moment from "moment";

interface Moment {
  _id: string;
  query: string;
  engine: string;
  location: string;
  screenshots: Array<Screenshot>;
}

interface Screenshot {
  id: string;
  date: string;
  positions: Array<{ link: URL; position: number }>;
}


@Component({
  components: {},
})
export default class Home extends Vue {


  private activeQueryButton = null;
  private backendUrl = `https://serp-master-app.herokuapp.com`;
  private moments: Array<Moment> = []

  private activeQuery: any = '';
  private hoverCell = 'google.com';

  private firstRun = true;

  private authorized = false;
  private credentials = {
    username: '',
    password: ''
  }

  @Watch('credentials.username')
  onUsernameChanged() {
    localStorage.username = this.credentials.username;
  }

  @Watch('credentials.password')
  onPasswordChanged() {
    localStorage.password = this.credentials.password;
  }

  @Watch('authorized')
  onAuthChanged() {
    localStorage.authorized = this.authorized;
  }

  private hostnameFromUrl(url: string) {
    return new URL(url).hostname;
  }

  private async getMoments() {
    if (!this.authorized) {
      // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
      //@ts-ignore
      this.$bvModal.show('auth')
      return;
    }
    console.log(`Getting moments from ${this.backendUrl}/moments`);
    const isQE = this.activeQuery;
    axios.get(`${this.backendUrl}/moments`, {
      auth: {
        username: this.credentials.username,
        password: this.credentials.password
      }
    })
        .then(
            (moments) => {
              this.authorized = true;
              return moments
            },
            () => {
              // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
              //@ts-ignore
              this.$bvModal.show('auth')
              this.makeToast(`Danger`, 'Danger', 'Wrong auth!');
              return Promise.reject();
            })
        .then((moments: { data: Array<Moment> }) => {
          console.log(`Moments:`)
          console.log(moments)
          this.moments = moments.data as Array<Moment>;
          if (!isQE) {
            this.activeQuery = this.googleQueries[0];
          } else {
            this.activeQuery = this.tablesQueries.find((query) => query.value.id === this.activeQuery.value.id)
          }
          console.log(`Done`)
        })
        .then(() => {
          if (!this.firstRun) {
            //do something
            return;
          } else {
            this.firstRun = false;
            this.makeToast(`success`, 'Success', 'Moments getted successfully')
          }
          // this.makeToast(`success`, 'Success', 'Moments getted successfully')
        })
        .catch((e: Error) => {
          console.log(e)
          this.makeToast(`danger`, 'Error', 'Thanos is happening.\nCan\t get moments')
        }).finally(() => {
      return true;
    })
  }

  mounted() {
    console.log(`Mounted`);
    if (localStorage.username && localStorage.password) {
      this.credentials.username = localStorage.username;
      this.credentials.password = localStorage.password;
      this.authorized = localStorage.authorized;
    } else {
      localStorage.setItem('username', this.credentials.username)
      localStorage.setItem('password', this.credentials.password);
      localStorage.setItem('authorized', 'false');
    }
    this.getMoments();
  }


  private highlightSimilar(data: string) {
    if (!+data) {

      this.hoverCell = data;
    }
    console.log(this.hoverCell)
  }


  get tablesByQuery() {
    if (this.activeQuery) {
      const StoreMoment = this.moments.find(
          (moment: Moment) => moment.query === this.activeQuery.text && moment.engine ===
              this.activeQuery.value.engine && moment.location === this.activeQuery.value.location);

      if (StoreMoment) {
        console.log(`Store moment:`);
        console.log(StoreMoment);
        const rows: Array<any> = [{position: 1}, {position: 2}, {position: 3}, {position: 4}, {position: 5}, {position: 6}, {position: 7}, {position: 8}, {position: 9}, {position: 10},];
        const fields: any = [{
          key: 'position',
          sortable: true,
          stickyColumn: true,
          // variant: 'success'
        },];
        for (let i = 0; i < 10; i++) {
          for (const screenshot of StoreMoment.screenshots) {
            fields.push({
              key: screenshot.date,
              label: moment(screenshot.date).format("DD.MM.YY hh:mm:ss"),
              sortable: false,
            })
            rows[i][screenshot.date] = screenshot.positions[i].link
          }
        }


        // StoreMoment.screenshots.forEach((screenshot, index) => {
        //   const row: any = {};
        //   row.position = index+1;
        //   screenshot.positions.forEach((position) => {
        //     row[moment(screenshot.date).format("DD.MM.YY hh:mm:ss")] = position.link;
        //   })
        //   rows[index] = row;
        // })
        // console.log(`rows`)
        // console.log(rows)
        return {
          location: StoreMoment.location,
          engine: StoreMoment.engine,
          fields: fields,
          // Array<{position:number, ...Array<{{date:Date:domain:URL}}>}>
          rows: rows
        }
      } else {
        return false;
      }
    } else {
      return false;
    }
  }

  private get tablesQueries() {
    return this.moments.map((moment: Moment) => {
          return {
            text: moment.query, value: {
              query: moment.query, engine: moment.engine, location: moment.location,
              id: moment._id
            }
          }
        }
    );
  }

  private get googleQueries() {
    return this.moments.filter((moment: Moment) => {
      return moment.engine === 'google.ru'
    }).map((moment: Moment) => {
          return {
            text: moment.query, value: {
              query: moment.query, engine: moment.engine, location: moment.location,
              id: moment._id
            }
          }
        }
    );
  }

  private get yandexQueries() {
    return this.moments.filter((moment: Moment) => {
      return moment.engine === 'yandex.ru'
    }).map((moment: Moment) => {
          return {
            text: moment.query, value: {
              query: moment.query, engine: moment.engine, location: moment.location,
              id: moment._id
            }
          }
        }
    );
  }

  private loading = false;

  private allowedEngines = ["google.ru", "yandex.ru"];

  get allowedLocations() {
    return this.createdEngine === "google.ru" ? ["Kyiv,Ukraine", "Moskow,Russia"] : [213, 143];
  }

  private createdQuery: string | null = null;
  private createdLocation = null;
  private createdEngine = "google.ru";

  private addQuery() {
    this.loading = true;
    axios.post(`${this.backendUrl}/moments`, {
      query: this.createdQuery?.trim(),
      engine: this.createdEngine,
      location: this.createdLocation,
      screenshots: []
    }, {
      auth: {
        username: this.credentials.username,
        password: this.credentials.password
      }
    }).then((saved: any) => {
      this.loading = false;
      this.hideModal()
      if (saved.data.message === 'error') {
        this.makeToast(`danger`, 'Create failed', 'Thanos is happening.\nCan\'t create query');
      } else if (saved.data.message === 'exist') {
        this.makeToast(`warning`, 'Create failed', 'This query already exist in DB.');
      } else {
        this.makeToast(`success`, 'Adding suceed', 'Successfull')
        this.getMoments()
      }
    })
  }

  private hideModal() {
    (this.$refs['modal'] as any).hide();
  }

  private async addScreenshotToQuery() {
    if (this.moments.find((Dmoment: Moment) => {
      return Dmoment.screenshots.find(
          (screenshot) => {
            return moment(screenshot.date).format("DD.MM.YY hh:mm:ss") ===
                moment().format("DD.MM.YY hh:mm:ss");
          })
    })) {
      this.makeToast(`warning`, 'Adding warning', 'Screenshot already exist');
      return;
    } else {
      this.loading = true;
      return await axios.post(`${this.backendUrl}/screenshot`, {
        query: this.activeQuery.value.query,
        engine: this.activeQuery.value.engine,
        location: this.activeQuery.value.location
      }, {
        auth: {
          username: this.credentials.username,
          password: this.credentials.password
        }
      }).then((saved: any) => {
        console.log(`saved:`)
        console.log(saved);
        if (saved.data.message === 'error') {
          console.log(`Something wrong here`);
          this.makeToast(`danger`, 'Adding failed', 'Thanos is happening.\nMaybe you are out of cost?')
        } else if (saved.data.message === 'success') {
          this.makeToast(`success`, 'Adding succeed', 'Successfully added screenshot');
        } else if (saved.data.message === 'exist') {
          this.makeToast(`warning`, 'Adding warning', 'Screenshot already exist');
        }

        this.loading = false;
      })
          .catch((e: Error) => {
            console.log(e)
          })
          .finally(() => {
            this.getMoments();
          })
    }
  }

  private async removeCurrentQuery() {
    this.loading = true;
    const id = this.moments.find((moment) => moment.query === this.activeQuery.value.query)!._id;
    if (id) {
      axios.delete(`${this.backendUrl}/moments/${id}`, {
        auth: {
          username: this.credentials.username,
          password: this.credentials.password
        }
      })
          .then((res) => {
            console.log(res)
          })
          .then(() => {
            this.loading = false;
            this.activeQuery = '';
          })
          .catch((e: Error) => {
            console.log(e)
          })
          .finally(() => {
            this.getMoments();

          })
    }
  }

  private makeToast(variant = 'secondary', title = '', message = '') {
    // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
    //@ts-ignore
    this.$bvToast.toast(message, {
      title: title,
      autoHideDelay: 5000,
      variant: variant
    })
  }
}
